export default {
  home_navigator: (state, data) => {
    state.Home.navigator = data
  },
  home_unregistered: (state, data) => {
    state.Home.unregistered = data
  },
  home_userInfo: (state, data) => {
    state.Home.userInfo = data
  }
}



